.header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin: 2.5rem 0.5rem;
}

.header__headline {
    margin: 0;

    color: hsl(0 0% 98%);
    font-size: 30px;
    letter-spacing: 6px;
}

@media(min-width: 992px) {
    .header{
        margin-top: 6rem;
    }   
}